@import url('https://fonts.googleapis.com/css?family=Quicksand:400,500,700');
.nav {
    width: 100%;
    height: 65px;
    position: fixed;
    line-height: 65px;
    text-align: center;
    position: fixed;
    padding: 5px 0px;
    background-color: transparent;
    z-index: 9999;
    transition: all .5s linear;
    
}

.nav div.logo {
    float: left;
    width: auto;
    height: auto;
    /* display: none; */
    padding-left: 3rem;
    margin-top: 5px;
    animation: logoanime 1s;
    animation-fill-mode: forwards;
}
@keyframes logoanime {
    from{
        display: none;
        transform: scale(1.5);
    }
    to
    {
        display: block;
        transform: scale(1);
    }
}

.ul22:hover
{
    background-color: white;
    color: #111;
}
.ul22
{
    border: 2px white solid;
    border-radius: 8px;

}


.nav
{
    background-image:linear-gradient(black,transparent);
}
.nav div.logo a {
    text-decoration: none;
    color: #fff;
    font-size: 20px;
}

