    transform-origin:100% 0% ;
}
.nav div.main_list ul li .ul1 {
    animation-delay: 0s;

}
.nav div.main_list ul li .ul2 {
    animation-delay: .2s;

}
.nav div.main_list ul li .ul3 {
    animation-delay: .4s;

}
.nav div.main_list ul li .ul4 {
    animation-delay: .6s;

}
.nav div.main_list ul li .ul5 {
    animation-delay: .8s;

}
.nav div.main_list ul li .ul6 {
    animation-delay: 1s;

}
.nav div.main_list ul li .ul7 {
    animation-delay: 1.2s;

}
@keyframes amoveup {
    from
    {
        opacity: 0;
        top:30px;
    }
    to
    {
        opacity: 1;
        top:0px;
        
    }
}
.nav div.main_list ul #marker
{
    position: absolute;
    height: 4px;
    width: 0px;
    left: 0px;
    background: rgb(239, 36, 36);
    bottom: 10px;
    border-radius: 5px;
    transition: all .5s;
}
